.buddy-container {
  width: 50%;
  height: 100%;
  position: relative;
  padding: 5px;
}
.buddy-container:hover {
  cursor: pointer;
}
.buddy-container:hover .buddy-wrapper .buddy-img {
  transform: scale(1.05);
  /* transform: translateY(-20px);
    box-shadow: 0px 0px 4px -4px rgb(); */
}
.buddy-wrapper {
  width: 100%;
  height: 55vw;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 10px;
  position: relative;
  overflow: hidden;
}
.buddy-wrapper:hover .buddy-img {
  filter: brightness(50%);
}
.buddy-img {
  position: absolute; /*to buddy-container, so wrapper will ignor it*/
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 0;
  transition: 0.3s;
  object-fit: cover;
}
.buddy-name {
  z-index: 1;
  opacity: 0;
  transition: 0.3s;
  color: white;
  font-weight: 400;
}
.buddy-wrapper:hover .buddy-name {
  opacity: 1;
}

.remove-buddy-btn {
  position: absolute;
  top: 2px;
  right: 2px;
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 0;
  font-size: 36px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  transition: 0.3s;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  padding: 0;
  color: #fff;
  cursor: pointer;
  background-color: transparent;
  color: red;
  z-index: 2;
}

/* Desktop */
@media only screen and (min-width: 768px) {
  .buddy-container {
    width: 25%;
  }
  .buddy-wrapper {
    height: 30vw;
    max-height: 340px;
  }
}
